<template>
    <div>
        <Header></Header>

        <section class="login_bg testr_ress">
            <div class="container">

                <div class="card_fathers ">
                    <div class="start_card">
                        <div class="start_card_header">
                            <div class="quiz_time">


                            </div>
                            <div class="quiz_count">
                                {{index_main+1}} / {{tests.questions.length}}
                            </div>

                        </div>
                        <div class="single_tests" v-show="index_main==index" v-for="(quiz,index) in tests.questions" :key="index">
                            <div class="single_title">
                              {{quiz.title}}
                            </div>
                           <div class="single_test_f">
                               <div class="single_tests">
                                   <button @click="get_ans=ans" class="test_choise" v-for="(ans,inds) in quiz.question_answers" :key="inds">
                                      {{ans.text}}
                                   </button>

                               </div>

                           </div>
                            <div class="prev_next_btn">
                                <div class="bottom_nextbtn prevbtns">
                                    <button @click="prevFunction(quiz,index)">

                                        <b-spinner v-if="show_spinner" variant="white" ></b-spinner>
                                        <span v-if="!show_spinner">   Ortga qaytish</span>
                                    </button>
                                </div>
                                <div class="bottom_nextbtn">
                                    <button @click="next_shows(quiz,index)">

                                        <b-spinner v-if="show_spinner" variant="white" ></b-spinner>
                                        <span v-if="!show_spinner">Davom etish</span>

                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


        </section>
        <Footer></Footer>

    </div>
</template>
<script>
    import Header from "../../components/Header";
    import Footer from "../../components/Footer"

    import html2pdf from "html2pdf.js";

    export default {
        components: {
            Header,
            Footer,

        },
        data() {
            return {
                start_test: true,
                is_active: 'NotGivn',
                tests: [],
                index_main: 0,
                answeer: [],
                renge_items: 20,
                is_results: false,
                result_mains: [],
                show_spinner: false,
                time: 1990, // 33 minutes and 10 seconds in total seconds
                timer: null, // To store the interval reference
                get_ans:'',
                result_id:'',
                test_id:''
            }
        },
        mounted() {
            this.startTimer()
            this.startFunction()
        },
        computed: {
            formattedTime() {
                const minutes = Math.floor(this.time / 60);
                const seconds = this.time % 60;
                return `${String(minutes).padStart(2, '0')}:${String(seconds).padStart(2, '0')}`;
            },
        },

        methods: {
            generatePDF() {
                const element = document.getElementById("pdf-content"); // Get the HTML content
                const options = {
                    margin: 1,
                    filename: "my-document.pdf",
                    image: { type: "jpeg", quality: 0.98 },
                    html2canvas: { scale: 3 },
                    jsPDF: { unit: "in", format: "letter", orientation: "portrait" },
                };

                // Use html2pdf to convert the content to PDF
                html2pdf().set(options).from(element).save();
            },
            startTimer() {
                if (this.timer) {
                    clearInterval(this.timer); // Clear any existing interval
                }
                this.timer = setInterval(() => {
                    if (this.time > 0) {
                        this.time--;
                    } else {
                        clearInterval(this.timer); // Stop the timer when it reaches 0
                        this.finished_tests()
                    }
                }, 1000);
            },
            next_shows(quiz) {
                this.test_id=quiz.id
                this.index_main++
                this.answeer.push({
                    "question_id": quiz.id,
                    "answer": this.is_active
                })
                console.log(this.answeer)
            },
            prevFunction() {
                this.show_spinner=true
                this.$axios.get(`test/questionnaire-active-test/?test_result_id=${this.result_id}&&test_id=${this.test_id}`, {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language': 'oz'
                    }
                    },

                )
                    .then(res => {
                        this.start_test = false
                        this.tests = res.data
                        this.show_spinner=false
                    })
            },
            startFunction() {
                this.show_spinner=true
                this.$axios.post('/test/test-start/', {
                        "test_category_id": this.$route.params.id
                    },
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                            'Accept-Language': 'oz'
                        }
                    }
                )
                    .then(res => {
                        this.start_test = false
                        this.tests = res.data
                        this.show_spinner=false
                        console.log(this.tests)
                        this.result_id=this.tests.result_id
                    })
            },
            finished_tests() {
                this.show_spinner=true

                this.$axios.post('/test/test-finish/', {
                        "answers": this.answeer,
                        "result_id": this.tests.result_id
                    },
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                            'Accept-Language': 'oz'
                        }
                    }
                )
                    .then(res => {
                        this.is_results=true
                        this.result_mains = res.data
                        this.show_spinner=false
                    })
            }
        },
        beforeDestroy() {
            if (this.timer) {
                clearInterval(this.timer); // Clear the interval when the component is destroyed
            }
        },

    }
</script>